import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Switch from "react-switch";
import Aux from "../../hoc/_Aux";
import ReactSelect from 'react-select'
import { validateCustomerCreation } from '../../validation/customer';

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getCustomerCategories } from '../../api/sales/customerCategory';
import Loader from '../../App/components/Reusables/Loader';
import { updateCustomer, getCustomer } from '../../api/sales/customer';
import { getCountries } from '../../api/misc';
import isEmpty from '../../validation/isEmpty';

const Update = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [customerCategories, setCustomerCategories] = React.useState([])

    const [submitting, setSubmitting] = React.useState(false)
    const [customerData, setCustomerData] = React.useState({
        id: '',
        fname: "",
        lname: '',
        mname: '',
        category: '',
        email: '',
        website: '',
        tax: '',
        phone1: '',
        phone2: '',
        address: '',
        country: '',
        countryName: '',
        isActive: false,
    })

    const [countryOptions, setCountryOptions] = React.useState([]);

    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
        const controller = new AbortController();


        (async () => {
            let ladder = 0;
            getCustomerCategories({ action: 'get', filter: 'none', start: 0, end: 999999 }, controller.signal)
                .then((res) => {
                    console.log(res)
                    if (res.json.status) {
                        setCustomerCategories([...res.json.customerCategories]);
                        setLoading(false);
                        ladder++;
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                }).catch((err) => {
                    console.log(err)
                });
            getCountries({ "action": "get" }, controller.signal)
                .then(res => {
                    console.log(res);
                    if (res.json.status) {
                        setCountryOptions([...res.json.countries.map(instance => ({ label: instance.name, value: instance.id }))])
                        ladder++;
                    }
                })
                .catch(err => {
                    console.log(err)
                })

            getCustomer({ action: 'get', customer_id: props.match.params.id, filter: 'none' }, controller.signal)
                .then(async (res) => {
                    console.log(res.json)
                    if (res.json.status) {
                        const customer = res.json.customers[0]
                        await setCustomerData({
                            ...customerData,
                            id: customer.id,
                            fname: customer.first_name,
                            lname: customer.last_name,
                            mname: customer.middle_name,
                            category: customer.category,
                            email: customer.email,
                            website: customer.website,
                            tax: customer.tax_number,
                            phone1: customer.phone1,
                            phone2: customer.phone2,
                            address: customer.address,
                            country: customer.country,
                            countryName: customer.country_detail.name,
                            isActive: customer.is_active

                        })
                        setLoading(false)
                    } else {
                        Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                    }
                }).catch((err) => {
                    console.log(err);
                });
        })()
        return () => controller.abort();
    }, []);

    const _selectedCountryCahnge = (country) => {
        setErrors({ ...errors, 'country': '' })
        setCustomerData({
            ...customerData,
            country: country.value,
            countryName: country.label,
        })
    }
    const _customerDataChange = (e) => {
        console.log('chaing', e.target.value);
        setErrors({ ...errors, [e.target.name]: '' })
        setCustomerData({ ...customerData, [e.target.name]: e.target.value })
    }
    const _activeChange = (checked) => {
        console.log(checked);
        setCustomerData({ ...customerData, isActive: checked })
    }


    const _submit = () => {
        //check if there are errors
        const { isValid, errors } = validateCustomerCreation(customerData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        console.log('no errros');

        setSubmitting(true)
        //if no errros api call
        const requestJson = {
            "action": "update",
            "id": customerData.id,
            "first_name": customerData.fname,
            "last_name": customerData.lname,
            "middle_name": customerData.mname,
            "category": parseInt(customerData.category),
            "email": customerData.email,
            "website": customerData.website,
            "tax_number": parseFloat(customerData.tax),
            "phone1": customerData.phone1,
            "phone2": customerData.phone2,
            "address": customerData.address,
            "country": customerData.country,
            "is_active": customerData.isActive,
        }

        updateCustomer(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'success', title: 'Customer updated successfully!', background: '#dffff0' })
                    props.history.push('/customers');
                } else {
                    setSubmitting(false);
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }


    console.log(customerData);

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Customer</h5>
                            <hr />
                            <Row>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>First Name*</Form.Label>
                                        <Form.Control name="fname" value={customerData.fname} onChange={_customerDataChange} type="text" placeholder="First Name" />
                                        <ErrorBody>{errors.fname}</ErrorBody>
                                    </Form.Group></Col>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>Middle Name</Form.Label>
                                        <Form.Control name="mname" value={customerData.mname || ''} onChange={_customerDataChange} type="text" placeholder="Middle Name" />
                                        <ErrorBody>{errors.mname}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={4}>
                                    <Form.Group  >
                                        <Form.Label>Last Name*</Form.Label>
                                        <Form.Control name="lname" value={customerData.lname} onChange={_customerDataChange} type="text" placeholder="Last Name" />
                                        <ErrorBody>{errors.lname}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group >
                                        <Form.Label>Customer Category*</Form.Label>
                                        <Form.Control name="category" value={customerData.category} onChange={_customerDataChange} as="select">

                                            <option value='' disabled >Select Customer Category</option>
                                            {customerCategories.map((cat, idex) => {
                                                return <option key={idex} value={cat.id}>{cat.name}</option>
                                            })}
                                        </Form.Control>
                                        <ErrorBody>{errors.category}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Email*</Form.Label>
                                        <Form.Control name="email" value={customerData.email} onChange={_customerDataChange} type="email" placeholder="Email" />
                                        <ErrorBody>{errors.email}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Address</Form.Label>
                                        <Form.Control name="address" value={customerData.address} onChange={_customerDataChange} type="text" placeholder="Address" />
                                        <ErrorBody>{errors.address}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Phone No 1*</Form.Label>
                                        <Form.Control name="phone1" value={customerData.phone1} onChange={_customerDataChange} type="tel" placeholder="Phone No 1" />
                                        <ErrorBody>{errors.phone1}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Phone No 2</Form.Label>
                                        <Form.Control name="phone2" value={customerData.phone2} onChange={_customerDataChange} type="tel" placeholder="Phone No 2" />
                                        <ErrorBody>{errors.phone2}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Tax Number*</Form.Label>
                                        <Form.Control name="tax" value={customerData.tax} onChange={_customerDataChange} type="number" placeholder="Tax Number" />
                                        <ErrorBody>{errors.tax}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Website</Form.Label>
                                        <Form.Control name="website" value={customerData.website} onChange={_customerDataChange} type="text" placeholder="Website" />
                                        <ErrorBody>{errors.website}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Country</Form.Label>
                                        <ReactSelect
                                            value={isEmpty(customerData.country) ? null : { value: customerData.country, label: customerData.countryName }}
                                            onChange={(instance) => _selectedCountryCahnge(instance)}
                                            options={countryOptions}
                                            placeholder='Select Country'
                                        />
                                        <ErrorBody>{errors.country}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group className="d-flex flex-column justify-content-end">
                                        <Form.Label style={{ marginBottom: '15px' }}>Is Active?</Form.Label>
                                        <Switch onChange={_activeChange} checked={customerData.isActive} />
                                    </Form.Group>
                                </Col>

                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/customers') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Updating</> : 'Update'}
                                </Button>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Update;
